<template>
  <div>
    <el-row>
      <el-button type="warning" @click="$router.push('/addClient')">新增</el-button>
    </el-row>
    <div>
      <ul v-for="c in clients">
        <li @click="$router.push({name:'getClientById',query:{precustomerno:c.precustomerno}})">{{c.precustomername}}</li>
        <li @click="$router.push({name:'getClientById',query:{precustomerno:c.precustomerno}})">联系人 {{c.linkman[0].linkman}}</li>
        <li @click="$router.push({name:'getClientById',query:{precustomerno:c.precustomerno}})">{{c.linkman[0].mobile}}</li>
        <hr>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "bClient",
  data(){
    return{
      clients:[],
    }
  },
  mounted() {
    this.show();
  },
  methods:{
    show(){
      var vm =this;
      this.axios({
        method: 'get',
        url: 'http://localhost:8081/selectClient'
      }).then(function (repos) {
        vm.clients=repos.data
        console.log(repos);
      })
    }
  }
}
</script>

<style scoped>
ul{
  list-style: none;
  width: 85%;
  margin-left: -10px;
}
li{
  text-align: left
}
.el-button{
  width: 100%;
}
</style>



