import Vue from 'vue'
import Router from 'vue-router'



import heads from "../components/heads";
import addClient from "../components/addClient";
import getClientById from "../components/getClientById";
import getzClientById from "../components/getzClientById";
import updateClient from "../components/updateClient";

import client from "../components/client";
import zClient from "../components/zClient";
import bClient from "../components/bClient";

// 解决ElementUI导航栏中的vue-router在3.0版本以上重复点菜单报错问题
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}


Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      name: 'heads',
      component: heads,
      props:true,
      children:[
        {path:'/heads/client',name:'client',component: client},
        {path:'/heads/zClient', name:'zClient', component: zClient},
        {path:'/heads/bClient',name:'bClient',component: bClient}
      ]
    },
    {
      path:'/addClient',
      name:'addClient',
      component: addClient
    },
    {
      path:'/getClientById',
      name:'getClientById',
      component:getClientById
    },
    {
      path:'/getzClientById',
      name:'getzClientById',
      component:getzClientById
    },
    {
      path:'/updateClient',
      name:'updateClient',
      component:updateClient
    }
  ]
})



