  }
}
</script>

<style>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
  .el-menu{
    width: 100%;
  }
  .el-menu-item{
    width: 33%;
    text-align: center;
    height: 26px;
    line-height: 10px;
  }
  .el-input{
    width:79%;
  }
</style>



