<template>
  <div>
    <el-container>
      <el-header style="height: 42px"><i class="el-icon-arrow-left" style="float: left; line-height:40px" @click="$router.push('/heads/BClient')"></i>客户资料</el-header>
    </el-container>
    <div style="align-content: center;height: 120px;">
      <img src="../images/准客户资料/u162.png" class="tu1">
      <img src="../images/准客户资料/u164.png" class="tu2">
    </div>
    <div style="margin-left: 30px">
      <p>保护期起止时间</p>
      <p>2020/03/28 18:20:34 至 2020/06/28 18:20:34</p>
    </div>
    <el-container>
      <el-header style="height: 12px"></el-header>
    </el-container>
    <div>
      <ul>
        <table>
          <tr>
            <td><li>客户名称</li></td>
            <td><span>{{clients.precustomername}}</span></td>
          </tr>
          <tr>
            <td><li>客户号</li></td>
            <td><span>{{clients.precustomerno}}</span></td>
          </tr>
          <tr>
            <td><li>客户星级</li></td>
            <td><span>3星用户</span></td>
          </tr>
          <tr>
            <td><li>投保意向</li></td>
            <td><span>{{clients.insure}}</span></td>
          </tr>
        </table>
      </ul>
      <div style="width: 100%; height: 30px;background-color: #ccc;line-height: 25px" >
        <span style="margin-left: 10px;font-size: 12px;">|&nbsp;&nbsp;&nbsp;基本信息</span>
      </div>
      <ul>
        <table>
          <tr>
            <td><li>证件类型</li></td>
            <td><span>{{clients.idtype}}</span></td>
          </tr>
          <tr>
            <td><li>行业类型</li></td>
            <td><span>{{clients.busicategory}}</span></td>
          </tr>
          <tr>
            <td><li>单位性质</li></td>
            <td><span>{{clients.grpnature}}</span></td>
          </tr>
          <tr>
            <td><li>证件号码</li></td>
            <td><span>{{clients.idno}}</span></td>
          </tr>
          <tr>
            <td><li>单位总人数</li></td>
            <td><span>{{clients.sumnumpeople}}人</span></td>
          </tr>
          <tr>
            <td><li>预计投保人数</li></td>
            <td><span>{{clients.presumpeople}}人</span></td>
          </tr>
          <tr>
            <td><li>单位地址</li></td>
            <td><span>{{clients.detailaddress}}</span></td>
          </tr>
        </table>
      </ul>
      <div style="width: 100%; height: 30px;background-color: #ccc;line-height: 25px" >
        <span style="margin-left: 10px;font-size: 12px;">|&nbsp;&nbsp;&nbsp;主联系人信息</span>
      </div>
      <ul>
        <table>
          <tr>
            <td><li>姓名</li></td>
            <td><span>{{clients.linkman[0].linkman}}</span></td>
          </tr>
          <tr>
            <td><li>手机</li></td>
            <td><span>{{clients.linkman[0].mobile}}</span></td>
          </tr>
          <tr>
            <td><li>办公电话</li></td>
            <td><span>{{clients.linkman[0].phone}}</span></td>
          </tr>
          <tr>
            <td><li>出生日期</li></td>
            <td><span>{{clients.linkman[0].birthday}} {{new Date().getFullYear() - new Date(clients.linkman[0].birthday.replace(/-/g, "/")).getFullYear()}}岁</span></td>
          </tr>
          <tr>
            <td><li>部门</li></td>
            <td><span>{{clients.linkman[0].depart}}</span></td>
          </tr>
          <tr>
            <td><li>职位</li></td>
            <td><span>{{clients.linkman[0].post}}</span></td>
          </tr>
          <tr>
            <td><li>邮箱</li></td>
            <td><span>{{clients.linkman[0].email}}</span></td>
          </tr>
        </table>
      </ul>
      <div style="width: 100%; height: 30px;background-color: #ccc;line-height: 25px" >
        <span style="margin-left: 10px;font-size: 12px;">|&nbsp;&nbsp;&nbsp;副联系人信息</span>
      </div>
      <ul>
        <table>
          <tr>
            <td><li>姓名</li></td>
            <td><span>{{clients.linkman[1].linkman}}</span></td>
          </tr>
          <tr>
            <td><li>手机</li></td>
            <td><span>{{clients.linkman[1].mobile}}</span></td>
          </tr>
          <tr>
            <td><li>办公电话</li></td>
            <td><span>{{clients.linkman[1].phone}}</span></td>
          </tr>
          <tr>
            <td><li>出生日期</li></td>
            <td><span>{{clients.linkman[1].birthday}} {{new Date().getFullYear() - new Date(clients.linkman[1].birthday.replace(/-/g, "/")).getFullYear()}}岁</span></td>
          </tr>
          <tr>
            <td><li>部门</li></td>
            <td><span>{{clients.linkman[1].depart}}</span></td>
          </tr>
          <tr>
            <td><li>职位</li></td>
            <td><span>{{clients.linkman[1].post}}</span></td>
          </tr>
          <tr>
            <td><li>邮箱</li></td>
            <td><span>{{clients.linkman[1].email}}</span></td>
          </tr>
        </table>
      </ul>
    </div>
    <el-form label-width="100px" class="demo-ruleForm" style="background-color: #ccc; height: 58px; padding-top: 1px">
      <el-form-item style="margin-top: 11px">
        <el-button type="primary" @click="select(clients.precustomerno)">编辑</el-button>
        <el-button @click="deletes(clients.precustomerno)">删除</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "getzClientById",
  data(){
    return{
      clients:[]
    }
  },
  mounted() {
    this.show()

  },
  methods:{
    show(){
      var vm = this
      var precustomerno = this.$route.query.precustomerno;
      this.axios.post("http://localhost:8081/getClientById?precustomerno="+precustomerno).then(function (repos) {
        vm.clients=repos.data
        console.log(vm.clients)
      })
    },
    select(precustomerno){
      location.href='/updateClient?precustomerno='+precustomerno;
    },
    deletes(precustomerno){
      this.axios.post("http://localhost:8081/deleteClient?precustomerno="+precustomerno).then(function (msg){
        if (msg.data == "1"){
          location.href="/heads/zClient"
        }
      })
    }

  }
}
</script>

<style scoped>
.el-header {
  background-color: #c9cacf;
  color: #333;
  text-align: center;
  line-height: 40px;
  height: 42px;
}
.tu1{
  float: left;
  margin-left: 44%;
  margin-top: 25px;
}
.tu2{
  float: left;
  margin-left: 47.2%;
  margin-top: -56px;
}
ul{
  list-style: none;
  width: 85%;
  margin-left: -10px;
}
li{
  text-align: left;

  float: left;
}
span{
  margin-left: 20px;
}
tr{

  height: 50px;
}
.el-form-item__content{
  margin-top: 11px;
}
</style>



