        nameMobileId:''
      }
    }
  },
  mounted() {
    this.show()

  },
  methods:{
    show(){
      var vm = this
      var precustomerno = this.$route.query.precustomerno;
      this.axios.post("http://localhost:8081/getClientById?precustomerno="+precustomerno).then(function (repos) {
        vm.ruleForm=repos.data
        console.log(vm.ruleForm)
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
          var vm = this
          var data = this.ruleForm
          this.axios.post('http://localhost:8081/updateClient',
            qs.stringify(data,{arrayFormat: 'indices', allowDots: true})).
          then(res=>{
            alert('submit!');
            vm.$router.push("/heads/zClient");
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    changeDisplay(){
      this.isDisplay = !this.isDisplay
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

